<template>
  <div class="form">
    <h1>Welcome to catmash!</h1>
    <h2>Vote for the cutest kitten</h2>
    <ul id="cats">

    </ul>
  </div>
</template>

<script> import $ from 'jquery' </script>
<script>
import cats from '../assets/data/cats.json'
export default {
  	name: 'Form',
	/*data: {
	    cats: []
	  },*/
	  mounted() {
	    var self = this;
	    /*$.getJSON('cats', function(data) {
	    console.log(data);
	      self.cats = data;
	    });*/

	    for(let i = 0; i < cats[0].length; i++)
			$("#cats").append("<li><img src='" + cats[i].url + "' id='" + cats[i].id + "' style='cursor:pointer' alt='click to vote for me'></li>");

  	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



